<script>
import { Bar } from 'vue-chartjs'
export default {
    extends: Bar,
    props:{
        data: Object,
        option: Object
    },
    mounted () {
        this.renderChart(this.data,{
            responsive: true,
            maintainAspectRatio: false,
            legend:{
                display: false
            }
        })
    }
}
</script>